import { request } from 'utils';

const prefix = '/api/finance';


export const getBills = ({page,pageSize=10,orderBy,isPagingEnabled=true} = {}) =>
  request({
    prefix,
    url: '/ReceiptConfig',
    method: 'GET',
    params: {page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });


export const getBillById = ({id} = {}) =>
  request({
    prefix,
    url: `/ReceiptConfig/${id}`,
    method: 'GET',
    params: {},
    data: {}
  });


export const createBill = ({
  label,
  invoiceTemplate,
  invoiceCode,
  previousRemainFrom,
  previousRemainTo,
  publishFrom,
  publishTo,
  noOfDelete,
  noOfLost,
  deletedReceipt,
  lostReceipt} = {}) =>
  request({
    prefix,
    url: '/ReceiptConfig',
    method: 'POST',
    params: {},
    data: {
      label,
      invoiceTemplate,
      invoiceCode,
      previousRemainFrom,
      previousRemainTo,
      publishFrom,
      publishTo,
      noOfDelete,
      noOfLost,
      deletedReceipt,
      lostReceipt
    }
  });


export const updateBill = ({
  id,
  label,
  invoiceTemplate,
  invoiceCode,
  previousRemainFrom,
  previousRemainTo,
  publishFrom,
  publishTo,
  noOfDelete,
  noOfLost,
  deletedReceipt,
  lostReceipt
} = {}) =>
  request({
  prefix,
  url: `/ReceiptConfig/${id}`,
  method: 'PUT',
  params: {},
  data: {
    id,
    label,
    invoiceTemplate,
    invoiceCode,
    previousRemainFrom,
    previousRemainTo,
    publishFrom,
    publishTo,
    noOfDelete,
    noOfLost,
    deletedReceipt,
    lostReceipt
  }
  });

export const deleteBill = (id) =>
  request({
  prefix,
  url:`/ReceiptConfig/${id}`,
  method: 'DELETE',
  params: {},
  data: {}
  });


